    if (!this.spinning) {
      this.spinning = true;

      const {
        pagination: {
          current,
          pageSize,
        },
      } = this;

      const params = {
        index: current || 1,
        size: pageSize || 10,
        isAsc: true,
        orderField: undefined,
      };

